import React from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'

import Header from './components/Header'
import Home from './pages/Home'
import Login from './pages/Login'
import AmazonTop100 from './pages/AmazonTop100'
import CustomsData from "./pages/CustomsData";
import Path from "./router/Path";

function App() {
    return (
        <Router>
            <Header/>
            <main className="h-main bg-background overflow-y-scroll">
                <Switch>
                    <Route path={Path.LogIn()}>
                        <Login/>
                    </Route>
                    <Route path={Path.AmazonTop100DashBoard()}>
                        <AmazonTop100/>
                    </Route>
                    <Route path={Path.CustomsDataDashBoard()}>
                        <CustomsData/>
                    </Route>
                    <Route path={Path.DashBoard()}>
                        <Home/>
                    </Route>
                </Switch>
            </main>
        </Router>
    );
}

export default App;
